.tag {
  display: inline-block;
  padding: 4px 10px;
  background-color: #4C9BE8;
  font-weight: lighter;
  font-size: 15px;
  text-align: center;
  border-radius: 6px;
  white-space: nowrap;
  filter: drop-shadow(3px 3px 10px rgba(0, 0, 0, 0.9));
  margin-bottom: 6px;
}

.tag:not(:last-child) {
  margin-right: 10px;
}